import {PRACTICE_STEPS} from "../js/refs";
import {Button, Modal} from "react-bootstrap";
import React, {useState} from "react";
import {TEXTS} from "../js/text";

const Steps = ({ activeStep, setActiveStep }) => {
    const [showModalCongratulations, setShowModalCongratulations] = useState(!1);
    const closeModalCongratulations = () => {
        setShowModalCongratulations(false);
    }
    const go = step => {
        setActiveStep(activeStep => activeStep + step);
    }
    return (
        <div>
            <div className="steps-container d-flex justify-content-center">
                {PRACTICE_STEPS.map((step, index) => (
                    <div key={index} className="step-item">
                        <div
                            className={`step-circle ${
                                index < activeStep
                                    ? "completed"
                                    : index === activeStep
                                        ? "active"
                                        : ""
                            }`}
                        >
                            {index + 1}
                        </div>
                        {index < PRACTICE_STEPS.length - 1 && (
                            <div
                                className={`step-line ${
                                    index < activeStep
                                        ? "completed"
                                        : ""
                                }`}
                            ></div>
                        )}
                    </div>
                ))}
            </div>
            <div className="text-center mt-4">
                {activeStep !== 0 &&
                    <Button
                        className="me-3" //  
                        onClick={() => go(-1)}
                    >
                        
                    </Button>
                }
                {activeStep !== PRACTICE_STEPS.length - 1
                    ? <Button
                        onClick={() => go(1)}
                    >
                        
                    </Button>
                    : <Button
                        variant="success"
                        onClick={() => setShowModalCongratulations(true)}
                    >
                        
                    </Button>
                }
            </div>
            <Modal show={showModalCongratulations} onHide={closeModalCongratulations}>
                <Modal.Header closeButton>
                    <Modal.Title>{TEXTS.DETAILS}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    ! ,       . <i className="bi bi-emoji-smile"></i>
                </Modal.Body>
                <Modal.Footer>
                    <Button
                        variant="secondary"
                        onClick={closeModalCongratulations}
                    >
                        {TEXTS.CLOSE}
                    </Button>
                </Modal.Footer>
            </Modal>
        </div>
    );
};


export default Steps;