import React, {useState} from 'react';
import MyNavbar from "../components/MyNavbar";
import {Button, Card, Container} from "react-bootstrap";
import {TEXTS} from "../js/text";
import {PRACTICE_STEPS} from "../js/refs";
import Steps from "../components/Steps";
import '../styles/Steps.css'

const Instructions = () => {
    const [activeStep, setActiveStep] = useState(0);
    return (
        <Container className="mt-3 text-center" fluid>
            <MyNavbar />
            <Card className="shadow-lg mt-5" border="secondary">
                <Card.Body>
                    <Card.Title>
                        <h1>{TEXTS.INSTRUCTIONS}</h1>
                    </Card.Title>
                    <Container className="mt-5">
                        <Card className="shadow-sm p-3 bg-light border-primary rounded-3">
                            <Card.Body>
                                <Steps
                                    activeStep={activeStep}
                                    setActiveStep={setActiveStep}
                                />
                            </Card.Body>
                        </Card>
                    </Container>
                </Card.Body>
            </Card>
        </Container>
    );
};

export default Instructions;